<template>
  <div class="box">
    <div
      v-if="certificate?.[0] && formAnswers?.[0]"
      class="flex row ra"
    >
      <button
        class="button is-primary"
        type="button"
        onclick="window.print()"
      >
        <span class="icon"><i class="fas fa-print" /></span>
        <span>Imprimir</span>
      </button>
    </div>
    <div
      v-if="certificate?.[0] && formAnswers?.[0]"
      id="printarea"
    >
      <div
        v-for="formAnswer in formAnswers"
        :key="formAnswer.id"
      >
        <div
          class="flex my-column"
        >
          <div
            class="flex row height-half"
          >
            <div
              class="flex my-column ca vca half-width"
              style="padding: 5mm;"
            >
              <img
                src="@/assets/ualg.svg"
                style="width: 80mm"
              >
              <div class="spacer" />
              <span><b>Deve ter este bilhete consigo no momento de entrada no recinto do evento.</b></span>
              <div class="half-spacer" />
              <span>O bilhete pode ser impresso ou usado em formato digital. Se perder o bilhete deve proceder ao pedido da emissão de um novo bilhete na plataforma de eventos.</span>
              <div class="half-spacer" />
              <span>O código QR neste bilhete serve como comprovativo de inscrição. Não deve partilhá-lo com ninguém, excepto com a organização ao fazer o registo da sua presença.</span>
            </div>
            <hr class="is-hidden-print">
            <div class="half-width">
              <div
                id="qrcode"
                class="flex my-column ca vca"
              >
                <h2 class="is-size-2">
                  Código de registo de presença
                </h2>
                <vue-qrcode
                  :value="formAnswer.id + '%' + formAnswer.authentication_key"
                  :options="{ width: 300 }"
                />
              </div>
            </div>
          </div>
          <hr class="is-hidden-print">
          <div
            class="flex row height-half"
          >
            <div
              class="flex my-column ca vca inverted half-width"
              style="padding: 5mm;"
            >
              <h2 class="is-size-3">
                Identificação
              </h2>
              <div class="spacer" />
              <h3 class="is-size-2">
                {{ formAnswer.author_first_name }} {{ formAnswer.author_last_name }}
              </h3>                
            </div>
            <hr class="is-hidden-print">
            <div
              class="flex my-column ca vca inverted  half-width"
              style="padding: 5mm;"
            >
              <h2 class="is-size-2">
                Dados do Evento
              </h2>
              <h3 class="is-size-3">
                {{ certificate[0].event_name }}
              </h3>
              <div class="spacer" />
              <span v-if="certificate[0].start"><b>Início esperado:</b> {{ certificate[0].start }}</span>
              <span v-if="certificate[0].end"><b>Fim esperado:</b> {{ certificate[0].end }}</span>
              <span v-if="certificate[0].location"><b>Localização:</b> {{ certificate[0].location }}</span>
              <br>
              <br>
              <span><b>Acesso Wi-Fi</b></span>
              <br>
              <span><b>SSID: </b> ualg-conferencias</span>
              <span><b>Password: </b> Não sei :(</span>
              <br><br>
              <span v-if="certificate[0].emergencyContact1 || certificate[0].emergencyContact2"><b>Contactos de emergência</b></span>
              <br>
              <span v-if="certificate[0].emergencyContact1">{{ certificate[0].emergencyContact1 }}</span>
              <span v-if="certificate[0].emergencyContact2">{{ certificate[0].emergencyContact2 }}</span>
            </div>
            <hr class="is-hidden-print">
          </div>
        </div>
      </div>
    </div>
    <div 
      v-else-if="certificate && !certificate?.[0]"
      class="flex column ca vca"
      style="height: 50vh;"
    >
      <span class="title">Este bilhete ainda não foi emitido</span>
    </div>
    <div 
      v-else-if="formAnswers && !formAnswers?.[0]"
      class="flex column ca vca"
      style="height: 50vh;"
    >
      <span class="title">Bilhete inválido ou indisponível</span>
    </div>
    <div
      v-else
      class="flex column ca vca"
      style="height: 50vh;"
    >
      <span class="title mb-4">A validar bilhete</span>
      <span
        class="loader mt-4"
        style="width: 100px; height: 100px;"
      />
    </div>
  </div>
</template>

<script>
import { API } from "@/http-common";
import VueQrcode from '@chenfengyuan/vue-qrcode';

export default {
  name: "ChooseForm",
  components: {VueQrcode},
  data() {
    return {
      formAnswers : undefined,
      certificate : undefined,
      event : undefined
    }
  },
  created() {
    document.title = "Bilhete de Acesso"
    if (this.$route.query.event) {
      API.get("ticket/?event=".concat(this.$route.query.event))
        .then((response) => {this.certificate = response.data; if (!this.certificate?.[0].available) this.certificate = [];}
      ).catch(() => this.formAnswers = []);
      API.get("event/".concat(this.$route.query.event))
        .then((response) => {
          this.event = response.data;
          API.get("formAnswerNoAuthAuthor/?event=".concat(this.$route.query.event)
            .concat("&form=").concat(response.data.formsignupid)
            .concat("&status=2,4")
          )
            .then((secondResponse) => {
              this.formAnswers = secondResponse.data;
            }
          ).catch(() => this.formAnswers = []);
        }
      ).catch(() => this.formAnswers = []);
    }
    else
      this.formAnswers = [];
  }
};
</script>

<style lang="scss">
  @import url(//db.onlinewebfonts.com/c/3b0ae6af94805b08b11cdf1d97c47dce?family=PreloSlabW01-Medium);

  #printarea>div {
    padding: 1cm;
  }
  #header-area {
    padding-left: 2mm;
    padding-right: 2mm;
  }

  #printarea .row {
    flex-direction: column;
  }


  @media print {
    .is-hidden-mobile, .is-hidden {
      display: flex !important;
    }

    .inverted {
        transform: rotate(180deg);
    }

    .is-hidden-print {
      display: none;
    }

    #printarea .row {
      flex-direction: row;
    }

    .half-width {
      width: 50%;
    }

    .height-half {
      height: 50vh;
    }

    .sb {
      justify-content: space-between !important;
    }

    #page-content {
      margin: 0;
    }
    #page-content * {
      visibility: hidden;
    }
    #printarea, #printarea * {
      visibility: visible;
    }

    #printarea {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
    }

    #printarea>div {
      height: 100vh;
      overflow: hidden;
      margin: 0;
      padding: 0;
    }
    
    #msg, #main-navbar, #top-hero, footer {
      display: none;
    }

    .date {
      margin-top: 0;
    }
  }

  #printarea {
    background-color: white;
  }
  h1, h2, h3, span {
    max-width: 20cm;
    text-align: center;
  }
  @page {
    size: A4;
  }

  .date {
    margin-top: 1cm;
  }
  
  h1, h2, h3 {
    margin: 3mm;
  }

  .half-spacer{
    height: 5mm;
  }

  .spacer{
    height: 1cm;
  }

  #printarea * {
    box-sizing: border-box;
    font-family: "PreloSlabW01-Medium";
    page-break-after: avoid;
  }

  .flex {
    display: flex;
  }

  .row {
    flex-direction: row;
  }

  .my-column {
    flex-direction: column;
  }

  .ca {
    justify-content: center;
  }

  .sb {
    justify-content: center;
  }

  .sa {
    justify-content: space-around;
  }

  .vca {
    align-items: center;
  }
</style>

<style lang="scss">
  @import '@/styles/flex-styles.scss';
  @import '@/styles/global-styles.scss';
</style>